import styled from "styled-components";

const OperateBtn = styled.div`
    float: right;
    padding: 0 10px;
    line-height: 20px;
    color: #53a3f6;
    border: 1px solid #53a3f6;
    border-radius: 4px;
`;
export default OperateBtn


