import styled from 'styled-components'
import { Link } from "react-router-dom";

const ChargeButton = styled(Link)`
    display: inline-block;
    background-color: #ff4500;
    color: #fff;
    padding: 0 10px;
    font-size: 12px;
    border-radius: 3px;
`;


export default ChargeButton


