import styled from "styled-components";
import { Link } from "react-router-dom";

const OperateBtnLink = styled(Link)`
    float: right;
    padding: 0 10px;
    line-height: 20px;
    color: #53a3f6;
    border: 1px solid #53a3f6;
    border-radius: 4px;
`;
export default OperateBtnLink;



